*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
  background-color: #F0EBD8;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.stop-scroll {
  margin: 0;
  height: 100%;
  overflow: hidden;
}

h1, h2, h3 {
  font-family: "DM Sans", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "DM Sans", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
}

.descripcion-pagina {
  text-align: center;
}

.titulo-pagina {
  font-weight: 700;
  padding-top: 2.5rem;
}

.logo {
  height: 10rem;
  width: 10rem;
  padding: 1rem;
}

/** Media Queries **/
.header {
  background-color: #748CAB;
}
.header .sidebar {
  display: none;
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9);
  scroll-behavior: unset;
}
.header .sidebar a {
  text-align: center;
  display: block;
  color: #FFFFFF;
  font-size: 3.4rem;
  font-weight: 500;
  background-color: #3E5C76;
  width: 80%;
  margin: 10rem auto 3rem auto;
  padding: 1rem;
  border-radius: 0.3rem;
  transition: all ease-in-out 0.3s;
}
.header .sidebar a:hover {
  background-color: #2c4255;
}
.header .sidebar a:last-of-type {
  margin-top: unset;
}
.header .sidebar.mostrar {
  display: flex;
  flex-direction: column;
}
.header .sidebar .cerrar-menu {
  width: 5rem;
  filter: invert(100%);
  margin: 0 auto;
  display: block;
  transition: all ease-in-out 0.3s;
}
.header .sidebar .cerrar-menu:hover {
  cursor: pointer;
  filter: invert(85%);
}

.contenedor__header {
  display: flex;
  justify-content: space-around;
  max-width: 80rem;
}
.contenedor__header .header__logo {
  flex-grow: 1;
}
.contenedor__header .header__menu {
  flex-grow: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contenedor__header .header__menu .menu {
  display: block;
  transition: all ease-in-out 0.3s;
  filter: invert(100%);
  width: 5rem;
}
@media (min-width: 768px) {
  .contenedor__header .header__menu .menu {
    display: none;
  }
}
.contenedor__header .header__menu .menu:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.contenedor__header .header__menu .menu.ocultar {
  filter: unset;
}
.contenedor__header .header__items {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-grow: 3;
}
.contenedor__header .header__items .item {
  display: none;
  text-decoration: none;
  list-style: none;
  color: #FFFFFF;
  font-size: 2rem;
  font-family: "DM Sans", sans-serif;
  font-weight: bold;
  transition: all ease-in-out 0.3s;
}
@media (min-width: 768px) {
  .contenedor__header .header__items .item {
    display: block;
  }
}
.contenedor__header .header__items .item:hover {
  color: #d9d9d9;
}

.form {
  display: flex;
  flex-direction: column;
  background-color: #3E5C76;
  width: 80%;
  margin: 0 auto;
  border-radius: 0.5rem;
  margin: 6rem auto;
  padding: 4rem;
}
.form .item__form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2.5rem;
  color: #FFFFFF;
  font-size: 1.7rem;
  font-weight: bold;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .form .item__form {
    flex-direction: row;
  }
}
.form .item__form label {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .form .item__form label {
    margin-bottom: 0rem;
  }
}
.form .item__form input {
  width: 80%;
  padding: 1rem;
  border-radius: 0.5rem;
}
.form .item__form textarea {
  min-width: 80%;
  max-width: 80%;
  padding: 1rem;
  resize: none;
  border-radius: 0.5rem;
}
.form .botones {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .form .botones {
    flex-direction: row;
  }
}
.form .botones .item__form--borrar {
  background-color: #760000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .form .botones .item__form--borrar {
    width: auto;
  }
}
.form .botones .item__form--borrar:hover {
  background-color: #430000;
  cursor: pointer;
}
.form .botones .item__form--send {
  background-color: #007800;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .form .botones .item__form--send {
    width: auto;
  }
}
.form .botones .item__form--send:hover {
  background-color: #004500;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form .botones .item__form--send {
    margin-bottom: unset;
  }
}

.footer_2 {
  background-color: #748CAB;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding-top: 2rem;
  margin: 3rem auto 0 auto;
}
@media (min-width: 768px) {
  .footer_2 {
    flex-direction: row;
  }
}
.footer_2 .footer__contacto {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 30px;
}
.footer_2 .footer__contacto p {
  font-size: 30px;
  text-align: center;
}
.footer_2 .footer__basico {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 20px;
}
.footer_2 .logo {
  width: 190px;
  height: 190px;
}

.copy {
  background-color: #647fa1;
  padding: 0.5rem;
}
.copy .descripcion-pagina {
  display: block;
  text-align: center;
  color: #f3f4f6;
}

.dioses {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
@media (min-width: 768px) {
  .dioses {
    grid-template-columns: repeat(3, 1fr);
  }
}
.dioses .dios {
  background-color: #f3f4f6;
  display: block;
  width: 80%;
  margin-bottom: 2rem;
  border-radius: 0.5rem;
  transition: all ease-in-out 0.3s;
}
.dioses .dios:hover {
  transform: scale(1.03);
}
.dioses .dios .dios__img {
  height: 50rem;
}
.dioses .dios .dios__desc,
.dioses .dios .dios__resumen {
  text-align: center;
}

.creadores {
  background-color: #1b2846;
  display: flex;
  flex-direction: column;
  margin-top: 3rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .creadores {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.creadores .creador {
  padding: 3rem;
  text-align: center;
  color: #FFFFFF;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .creadores .creador {
    font-size: 2.5rem;
  }
}
.creadores .creador img {
  height: 17rem;
  width: 17rem;
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .creadores .creador img {
    height: 27rem;
    width: 27rem;
  }
}
.creadores .creador .creador__nombre {
  margin: 1rem;
}

.motivo {
  background-color: #0d1321;
  margin: 3rem auto;
  padding: 2rem;
  text-align: center;
  border-radius: 0.5rem;
}
.motivo p {
  color: #FFFFFF;
  font-size: 2.5rem;
}